import express from 'express';
import * as path from 'path';
import * as Dotenv from "dotenv"
import mongoose from "mongoose"
import { UserRouter } from "./router/User"
import { MFARouter } from './router/MFA';
import bodyParser = require('body-parser');
import cors from 'cors';
Dotenv.config()
const app = express();
const port = process.env.PORT || 3333;
app.use(cors());
app.use(express.json())
app.use(bodyParser.json())
app.use(express.json({
  type: ['application/json', 'text/plain']
}))
app.use('/assets', express.static(path.join(__dirname, 'assets')));
app.use("/MFAPI", MFARouter)
app.use("/UserAPI", UserRouter)

app.get('/api', (req, res) => {
  res.status(200).send({ message: 'Welcome to backend!' });
});

mongoose.set('strictQuery', false)

mongoose.connect(process.env.MONGOOSE_CONNECTION_STRING).then(() => {
  app.listen(port, () => {
    /* eslint-disable */;(oo_oo(),console.log(`Listening at ${location.hostname}:${port}/api, connected successfully to database`, `2d62e18f_0`));
  }).on('error', console.error);
}).catch(() => {
  /* eslint-disable */;(oo_oo(),console.log("Connection failed, server terminated", `2d62e18f_1`))
})

